html, body {
	margin: 0;
	padding: 0;
	height: 100%;
	min-height: 100%;
	font-family: "Exo";
	display: block;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
  }
  
  section {
	  position: relative;
	  display: block;
  }
  
  a {
	  text-decoration: none;
  }
  
  .flex {
	  display: flex;
	  flex-wrap: wrap;
  }
  .content-wr {
	  position: relative;
	  max-width: 1200px;
	  margin: 0 auto;
  }


